export class Time {
    constructor() {
        this.tm = 1 / 3;
        this.im = 0;
        this.h = 0;
        this.sm = 0;
        this.hm = 0;
        this.rm = 0;
        this.am = 1;
    }
    start() {
        this.im = performance.now();
    }
    update() {
        const t = performance.now();
        let e = (t - this.im) / 1e3;
        if (this.tm < e) {
            e = this.tm;
        }
        this.rm = e;
        this.sm += e;
        this.hm = e * this.am;
        this.h += this.hm;
        this.im = t;
    }
    get time() {
        return this.h;
    }
    get unscaledTime() {
        return this.sm;
    }
    get deltaTime() {
        return this.hm;
    }
    get unscaledDeltaTime() {
        return this.rm;
    }
    get timeScale() {
        return this.am;
    }
    set timeScale(t) {
        this.am = t;
    }
    get maximumDeltaTime() {
        return this.tm;
    }
    set maximumDeltaTime(t) {
        this.tm = t;
    }
}