import { InputHandler } from "./input/InputHandler";

import { Instantiater } from "./Instantiater";

export class EngineGlobalObject {
    constructor(t, e, r, s, i, n, h, a, o, u) {
        this.t = t;
        this.i = e;
        this.h = r;
        this.o = s;
        this.u = i;
        this.g = n;
        this.p = h;
        this.m = a;
        this.l = o;
        this.I = new InputHandler(u);
        this.P = new Instantiater(this);
    }
    applyGameSetting(t) {
        this.l.applyPhysicsSettings(t.physics);
    }
    dispose() {
        this.I.dispose();
    }
    get scene() {
        return this.t;
    }
    get cameraContainer() {
        return this.i;
    }
    get screen() {
        return this.u;
    }
    get input() {
        return this.I;
    }
    get time() {
        return this.h;
    }
    get physics() {
        return this.l;
    }
    get gameState() {
        return this.o;
    }
    get instantiater() {
        return this.P;
    }
    get sceneProcessor() {
        return this.g;
    }
    get coroutineProcessor() {
        return this.p;
    }
    get transformMatrixProcessor() {
        return this.m;
    }
    get physics2DProcessor() {
        return this.l;
    }
}