import { ZaxisSortable } from "./ZaxisSortable";

export class ZaxisInitializer extends ZaxisSortable {
    constructor() {
        super(...arguments);
        this.disallowMultipleComponent = true;
        this.Za = true;
    }
    start() {
        this.qa();
        if (this.runOnce) this.enabled = false;
    }
    update() {
        this.qa();
    }
    qa() {
        this.gameObject.getComponentsInChildren().forEach((t => {
            const i = t;
            if (i.onSortByZaxis) {
                if (typeof i.onSortByZaxis === "function") {
                    i.onSortByZaxis(t.transform.position.z);
                }
            }
        }));
    }
    static checkAncestorZaxisInitializer(t, i) {
        if (!t.transform.parent) return;
        let e = t.transform.parent;
        while (e) {
            const t = e.gameObject.getComponent(ZaxisInitializer);
            if (t) {
                i(e.position.z);
                return;
            }
            if (e.parent === null) break;
            e = e.parent;
        }
    }
    get runOnce() {
        return this.Za;
    }
    set runOnce(t) {
        this.Za = t;
    }
}