import { Euler, Matrix4, Quaternion } from "three/src/Three";

import { clamp } from "three/src/math/MathUtils";

export class ObservableEuler {
    constructor(t = 0, s = 0, h = 0, i = ObservableEuler.DefaultOrder) {
        this.isEuler = true;
        this.th = t;
        this.sh = s;
        this.hh = h;
        this.ih = i;
        this._onChangeCallback = () => {};
        this.eh = () => {};
        this.rh = () => {};
    }
    onBeforeGetComponent(t) {
        this.rh = t;
    }
    onBeforeChange(t) {
        this.eh = t;
    }
    get _x() {
        this.rh();
        return this.th;
    }
    set _x(t) {
        this.th = t;
    }
    get _y() {
        this.rh();
        return this.sh;
    }
    set _y(t) {
        this.sh = t;
    }
    get _z() {
        this.rh();
        return this.hh;
    }
    set _z(t) {
        this.hh = t;
    }
    get _order() {
        this.rh();
        return this.ih;
    }
    set _order(t) {
        this.ih = t;
    }
    get x() {
        this.rh();
        return this.th;
    }
    set x(t) {
        if (this.th === t) return;
        this.eh();
        this.th = t;
        this._onChangeCallback();
    }
    get y() {
        this.rh();
        return this.sh;
    }
    set y(t) {
        if (this.sh === t) return;
        this.eh();
        this.sh = t;
        this._onChangeCallback();
    }
    get z() {
        this.rh();
        return this.hh;
    }
    set z(t) {
        if (this.hh === t) return;
        this.eh();
        this.hh = t;
        this._onChangeCallback();
    }
    get order() {
        this.rh();
        return this.ih;
    }
    set order(t) {
        if (this.ih === t) return;
        this.eh();
        this.ih = t;
        this._onChangeCallback();
    }
    set(t, s, h, i) {
        if (t === this.th && s === this.sh && h === this.hh && i === this.ih) return this;
        this.eh();
        this.th = t;
        this.sh = s;
        this.hh = h;
        if (i) {
            this.ih = i;
        }
        this._onChangeCallback();
        return this;
    }
    clone() {
        this.rh();
        return new Euler(this.th, this.sh, this.hh, this.ih);
    }
    copy(t) {
        if (this.th === t.th && this.sh === t.sh && this.hh === t.hh && this.ih === t.ih) return this;
        this.eh();
        this.th = t._x;
        this.sh = t._y;
        this.hh = t._z;
        this.ih = t._order;
        this._onChangeCallback();
        return this;
    }
    setFromRotationMatrix(t, s, h = true) {
        this.rh();
        const i = t.elements;
        const e = i[0], r = i[4], a = i[8];
        const n = i[1], o = i[5], u = i[9];
        const M = i[2], c = i[6], l = i[10];
        if (!s) s = this.ih;
        this.eh();
        switch (s) {
          case "XYZ":
            this.sh = Math.asin(clamp(a, -1, 1));
            if (Math.abs(a) < .9999999) {
                this.th = Math.atan2(-u, l);
                this.hh = Math.atan2(-r, e);
            } else {
                this.th = Math.atan2(c, o);
                this.hh = 0;
            }
            break;

          case "YXZ":
            this.th = Math.asin(-clamp(u, -1, 1));
            if (Math.abs(u) < .9999999) {
                this.sh = Math.atan2(a, l);
                this.hh = Math.atan2(n, o);
            } else {
                this.sh = Math.atan2(-M, e);
                this.hh = 0;
            }
            break;

          case "ZXY":
            this.th = Math.asin(clamp(c, -1, 1));
            if (Math.abs(c) < .9999999) {
                this.sh = Math.atan2(-M, l);
                this.hh = Math.atan2(-r, o);
            } else {
                this.sh = 0;
                this.hh = Math.atan2(n, e);
            }
            break;

          case "ZYX":
            this.sh = Math.asin(-clamp(M, -1, 1));
            if (Math.abs(M) < .9999999) {
                this.th = Math.atan2(c, l);
                this.hh = Math.atan2(n, e);
            } else {
                this.th = 0;
                this.hh = Math.atan2(-r, o);
            }
            break;

          case "YZX":
            this.hh = Math.asin(clamp(n, -1, 1));
            if (Math.abs(n) < .9999999) {
                this.th = Math.atan2(-u, o);
                this.sh = Math.atan2(-M, e);
            } else {
                this.th = 0;
                this.sh = Math.atan2(a, l);
            }
            break;

          case "XZY":
            this.hh = Math.asin(-clamp(r, -1, 1));
            if (Math.abs(r) < .9999999) {
                this.th = Math.atan2(c, o);
                this.sh = Math.atan2(a, e);
            } else {
                this.th = Math.atan2(-u, l);
                this.sh = 0;
            }
            break;

          default:
            console.warn("THREE.Euler: .setFromRotationMatrix() encountered an unknown order: " + s);
        }
        this.ih = s;
        if (h === true) this._onChangeCallback();
        return this;
    }
    setFromQuaternion(t, s, h) {
        _matrix.makeRotationFromQuaternion(t);
        return this.setFromRotationMatrix(_matrix, s, h);
    }
    setFromVector3(t, s) {
        return this.set(t.x, t.y, t.z, s);
    }
    reorder(t) {
        _quaternion.setFromEuler(this);
        return this.setFromQuaternion(_quaternion, t);
    }
    equals(t) {
        this.rh();
        return t._x === this.th && t._y === this.sh && t._z === this.hh && t._order === this.ih;
    }
    fromArray(t) {
        if (this.th === t[0] && this.sh === t[1] && this.hh === t[2] && this.ih === t[3]) return this;
        this.eh();
        this.th = t[0];
        this.sh = t[1];
        this.hh = t[2];
        if (t[3] !== undefined) this.ih = t[3];
        this._onChangeCallback();
        return this;
    }
    toArray(t = [], s = 0) {
        this.rh();
        t[s] = this.th;
        t[s + 1] = this.sh;
        t[s + 2] = this.hh;
        t[s + 3] = this.ih;
        return t;
    }
    _onChange(t) {
        this._onChangeCallback = t;
        return this;
    }
}

ObservableEuler.DefaultOrder = "XYZ";

ObservableEuler.RotationOrders = [ "XYZ", "YZX", "ZXY", "XZY", "YXZ", "ZYX" ];

const _matrix = new Matrix4;

const _quaternion = new Quaternion;