import { Vector2 } from "three/src/Three";

import { CSS3DObject } from "../../render/CSS3DRenderer";

import { Component } from "../../hierarchy_object/Component";

import { Transform } from "../../hierarchy_object/Transform";

import { ZaxisInitializer } from "./ZaxisInitializer";

export class CssRenderer extends Component {
    constructor() {
        super(...arguments);
        this.css3DObject = null;
        this.htmlElement = null;
        this.fa = new Vector2;
        this.Da = .1;
        this.Gr = true;
        this._n = 0;
        this.Ea = false;
    }
    start() {
        this.Ea = true;
        this.renderInitialize();
        ZaxisInitializer.checkAncestorZaxisInitializer(this.gameObject, this.onSortByZaxis.bind(this));
    }
    onDestroy() {
        if (this.css3DObject) {
            this.transform.dequeueRenderAttachedObject3D(this.css3DObject);
            this.transform.unsafeGetObject3D().remove(this.css3DObject);
            this.css3DObject = null;
            this.htmlElement = null;
        }
    }
    onEnable() {
        if (this.css3DObject) {
            this.css3DObject.visible = true;
            this.transform.enqueueRenderAttachedObject3D(this.css3DObject);
        }
    }
    onDisable() {
        if (this.css3DObject) {
            this.css3DObject.visible = false;
            this.transform.enqueueRenderAttachedObject3D(this.css3DObject);
        }
    }
    onSortByZaxis(t) {
        this._n = t / .1;
        if (this.css3DObject) {
            this.css3DObject.element.style.zIndex = Math.floor(this._n).toString();
        }
    }
    onWorldMatrixUpdated() {
        if (this.css3DObject) {
            Transform.updateRawObject3DWorldMatrixRecursively(this.css3DObject);
            this.transform.enqueueRenderAttachedObject3D(this.css3DObject);
        }
    }
    renderInitialize() {
        throw new Error("Yon can't Use CssRenderer Directly, use Derived Class e.g. CssSpriteRenderer");
    }
    initializeBaseComponents(t) {
        if (!this.htmlElement) throw new Error("htmlElement is null");
        let e = false;
        if (t) {
            if (this.css3DObject) {
                this.transform.unsafeGetObject3D().remove(this.css3DObject);
            }
            this.css3DObject = new CSS3DObject(this.htmlElement);
            e = true;
        } else {
            if (this.css3DObject) {
                this.css3DObject.element = this.htmlElement;
            } else {
                this.css3DObject = new CSS3DObject(this.htmlElement);
                e = true;
            }
        }
        if (e) {
            this.htmlElement.style.pointerEvents = this.pointerEvents ? "auto" : "none";
            this.htmlElement.style.zIndex = Math.floor(this._n).toString();
            if (this.enabled && this.gameObject.activeInHierarchy) this.css3DObject.visible = true; else this.css3DObject.visible = false;
        }
        this.updateViewScale(false);
        this.updateCenterOffset(false);
        this.transform.unsafeGetObject3D().add(this.css3DObject);
        return this.css3DObject;
    }
    updateCenterOffset(t) {
        throw new Error("Yon can't Use CssRenderer Directly, use Derived Class e.g. CssSpriteRenderer");
    }
    updateViewScale(t) {
        throw new Error("Yon can't Use CssRenderer Directly, use Derived Class e.g. CssSpriteRenderer");
    }
    get centerOffset() {
        return this.fa;
    }
    set centerOffset(t) {
        this.fa.copy(t);
        this.updateCenterOffset(true);
    }
    get viewScale() {
        return this.Da;
    }
    set viewScale(t) {
        this.Da = t;
        this.updateViewScale(true);
    }
    get pointerEvents() {
        return this.Gr;
    }
    set pointerEvents(t) {
        this.Gr = t;
        if (this.htmlElement) {
            this.htmlElement.style.pointerEvents = t ? "auto" : "none";
        }
    }
    get readyToDraw() {
        return this.Ea;
    }
}