import { Scene as ThreeScene } from "three/src/Three";

import { Transform } from "./Transform";

export class Scene {
    constructor() {
        this.fe = new ThreeScene;
        this.fe.matrixAutoUpdate = false;
        this.fe.autoUpdate = false;
    }
    addChildFromBuilder(e) {
        const r = e.build(null);
        e.processEvent();
        return r;
    }
    foreachChild(e) {
        const r = this.fe.children;
        for (let t = 0, n = r.length; t < n; t++) {
            const n = r[t];
            if (n.userData instanceof Transform) {
                e(n.userData);
            }
        }
    }
    iterateChild(e) {
        const r = this.fe.children;
        for (let t = 0, n = r.length; t < n; t++) {
            const n = r[t];
            if (n.userData instanceof Transform) {
                if (!e(n.userData)) break;
            }
        }
    }
    get children() {
        return this.fe.children.filter((e => e.userData instanceof Transform)).map((e => e.userData));
    }
    unsafeGetThreeScene() {
        return this.fe;
    }
}