import { Color } from "./Color";

export class CameraInfo {
    constructor(r, o) {
        this.Dt = r;
        this.Cr = (new Color).copy(o);
    }
    get priority() {
        return this.Dt;
    }
    set priority(r) {
        this.Dt = r;
    }
    get backgroundColor() {
        return this.Cr;
    }
    set backgroundColor(r) {
        this.Cr.copy(r);
    }
}