import { SceneBuilder } from "./SceneBuilder";

import { GameSetting } from "./setting/GameSetting";

export class Bootstrapper {
    constructor(t, e) {
        this.P = t.instantiater;
        this.L = e || null;
        this.U = new SceneBuilder(t.sceneProcessor);
        this.S = new GameSetting(GameSetting.createDefaultObject());
    }
    getGameSettingObject() {
        Object.freeze(this.S);
        return this.S.make();
    }
    get instantiater() {
        return this.P;
    }
    get interopObject() {
        return this.L;
    }
    get sceneBuilder() {
        return this.U;
    }
    get setting() {
        return this.S;
    }
}