import { ComponentEvent } from "./ComponentEvent";

import { MutIteratableCollection } from "../collection/MutIteratableCollection";

function isOnWorldMatrixUpdatedableComponent(t) {
    return t.onWorldMatrixUpdated !== undefined;
}

export class GameObjectEventContainer {
    constructor() {
        this.ti = new Set;
        this.ii = new MutIteratableCollection(ComponentEvent.comparator);
        this.ei = new MutIteratableCollection(ComponentEvent.comparator);
        this.ni = new MutIteratableCollection(ComponentEvent.comparator);
        this.oi = new MutIteratableCollection(ComponentEvent.comparator);
        this.ri = new MutIteratableCollection(ComponentEvent.comparator);
        this.si = new MutIteratableCollection(ComponentEvent.comparator);
    }
    registerComponent(t) {
        if (isOnWorldMatrixUpdatedableComponent(t)) {
            this.ti.add(t);
        }
        const i = t._engine_internal_componentEventContainer;
        if (i.onCollisionEnter2D) {
            this.ii.insert(i.onCollisionEnter2D);
        }
        if (i.onCollisionStay2D) {
            this.ei.insert(i.onCollisionStay2D);
        }
        if (i.onCollisionExit2D) {
            this.ni.insert(i.onCollisionExit2D);
        }
        if (i.onTriggerEnter2D) {
            this.oi.insert(i.onTriggerEnter2D);
        }
        if (i.onTriggerStay2D) {
            this.ri.insert(i.onTriggerStay2D);
        }
        if (i.onTriggerExit2D) {
            this.si.insert(i.onTriggerExit2D);
        }
    }
    unregisterComponent(t) {
        if (isOnWorldMatrixUpdatedableComponent(t)) {
            this.ti.delete(t);
        }
        const i = t._engine_internal_componentEventContainer;
        if (i.onCollisionEnter2D) {
            this.ii.delete(i.onCollisionEnter2D);
        }
        if (i.onCollisionStay2D) {
            this.ei.delete(i.onCollisionStay2D);
        }
        if (i.onCollisionExit2D) {
            this.ni.delete(i.onCollisionExit2D);
        }
        if (i.onTriggerEnter2D) {
            this.oi.delete(i.onTriggerEnter2D);
        }
        if (i.onTriggerStay2D) {
            this.ri.delete(i.onTriggerStay2D);
        }
        if (i.onTriggerExit2D) {
            this.si.delete(i.onTriggerExit2D);
        }
    }
    invokeOnWorldMatrixUpdated() {
        for (const t of this.ti) {
            t.onWorldMatrixUpdated();
        }
    }
    invokeOnCollisionEnter2D(t) {
        if (this.ii.size === 0) return;
        this.ii.forEach((i => i.invoke(t)));
    }
    invokeOnCollisionStay2D(t) {
        if (this.ei.size === 0) return;
        this.ei.forEach((i => i.invoke(t)));
    }
    invokeOnCollisionExit2D(t) {
        if (this.ni.size === 0) return;
        this.ni.forEach((i => i.invoke(t)));
    }
    invokeOnTriggerEnter2D(t) {
        if (this.oi.size === 0) return;
        this.oi.forEach((i => i.invoke(t)));
    }
    invokeOnTriggerStay2D(t) {
        if (this.ri.size === 0) return;
        this.ri.forEach((i => i.invoke(t)));
    }
    invokeOnTriggerExit2D(t) {
        if (this.si.size === 0) return;
        this.si.forEach((i => i.invoke(t)));
    }
}