export class ComponentEvent {
    constructor(t, e, n, r) {
        this.isRemoved = false;
        this.Dt = n;
        this.kt = t.generateEventId();
        this.At = r;
        this.Ut = e;
    }
    static createAwakeEvent(t, e) {
        return new ComponentEvent(t, e, 0, 0);
    }
    static createStartEvent(t, e, n) {
        return new ComponentEvent(t, e, 2, n);
    }
    static createUpdateEvent(t, e, n) {
        return new ComponentEvent(t, e, 3, n);
    }
    static createOnEnableEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    static createOnDisableEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    static createOnDestroyEvent(t, e, n) {
        return new ComponentEvent(t, e, 4, n);
    }
    static createOnCollisionEnter2DEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    static createOnCollisionStay2DEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    static createOnCollisionExit2DEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    static createOnTriggerEnter2DEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    static createOnTriggerStay2DEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    static createOnTriggerExit2DEvent(t, e, n) {
        return new ComponentEvent(t, e, 1, n);
    }
    get invoke() {
        return this.Ut;
    }
    static comparator(t, e) {
        if (t.Dt === e.Dt) {
            if (t.At === e.At) {
                return t.kt - e.kt;
            } else {
                return t.At - e.At;
            }
        }
        return t.Dt - e.Dt;
    }
}