import { GameObjectBuilder } from "./hierarchy_object/GameObjectBuilder";

export class Instantiater {
    constructor(t) {
        this.B = 0;
        this._ = 0;
        this.T = t;
    }
    generateId() {
        const t = this.B;
        this.B += 1;
        return t;
    }
    generateEventId() {
        const t = this._;
        this._ += 1;
        return t;
    }
    buildGameObject(t, e, r, i) {
        return new GameObjectBuilder(this.T, t, e, r, i);
    }
    buildPrefab(t, e, r, i, s) {
        return new e(this.T, t, r, i, s);
    }
}