import { WaitForEndOfFrame, WaitForSeconds, WaitUntil, WaitWhile } from "./YieldInstruction";

export class CoroutineProcessor {
    constructor(t) {
        this.h = t;
        this.Wt = [];
        this.Ft = 0;
    }
    addCoroutine(t) {
        this.Wt.push(t);
        this.Ft += 1;
    }
    removeCoroutine(t) {
        const i = this.Wt.indexOf(t);
        if (i >= 0) {
            this.Wt[i] = null;
            this.Ft -= 1;
        }
    }
    tryCompact() {
        if (CoroutineProcessor._needToCompactCount <= this.Wt.length - this.Ft) {
            let t = 0;
            for (let i = 0; i < this.Wt.length; i++) {
                const s = this.Wt[i];
                if (s === null) continue;
                this.Wt[t] = s;
                t += 1;
            }
            this.Wt.length = this.Ft;
        }
    }
    updateAfterProcess() {
        const t = this.Wt;
        for (let i = 0; i < t.length; i++) {
            const s = t[i];
            if (s == null) continue;
            if (s.currentYieldInstructionExist) {
                this.Pt(s);
            } else {
                t[i] = null;
                this.Ft -= 1;
            }
        }
    }
    Pt(t) {
        const i = t.currentYieldInstruction;
        if (i instanceof WaitForSeconds) {
            t.elapsedTime += this.h.deltaTime;
            if (t.elapsedTime >= i.seconds) {
                t.elapsedTime = 0;
                t.fatchNextInstruction();
            }
        } else if (i instanceof WaitUntil) {
            if (i.predicate()) {
                t.fatchNextInstruction();
            }
        } else if (i instanceof WaitWhile) {
            if (!i.predicate()) {
                t.fatchNextInstruction();
            }
        } else if (i === null) {
            t.fatchNextInstruction();
        }
    }
    endFrameAfterProcess() {
        const t = this.Wt;
        for (let i = 0; i < t.length; i++) {
            const s = t[i];
            if (s == null) continue;
            if (s.currentYieldInstructionExist) {
                this.Ct(s);
            } else {
                t[i] = null;
                this.Ft -= 1;
            }
        }
    }
    Ct(t) {
        const i = t.currentYieldInstruction;
        if (i instanceof WaitForEndOfFrame) {
            t.fatchNextInstruction();
        }
    }
}

CoroutineProcessor._needToCompactCount = 16;