import { GameObjectBuilder } from "../hierarchy_object/GameObjectBuilder";

export class SceneBuilder {
    constructor(t) {
        this.g = t;
        this.V = [];
    }
    withChild(t) {
        this.V.push(t);
        return this;
    }
    build() {
        for (let t = 0; t < this.V.length; t++) {
            this.V[t].build(null);
        }
        GameObjectBuilder.processEventByGroup(this.V, this.g);
    }
}