import { EventContainer } from "../collection/EventContainer";

export class GameScreen {
    constructor(t, e) {
        this.$i = t;
        this._e = e;
        this.Ne = new EventContainer;
    }
    get width() {
        return this.$i;
    }
    get height() {
        return this._e;
    }
    get onResize() {
        return this.Ne;
    }
    resize(t, e) {
        this.$i = t;
        this._e = e;
        this.Ne.invoke(t, e);
    }
}