import { MutIteratableCollection } from "./collection/MutIteratableCollection";

import { ComponentEvent } from "./hierarchy_object/ComponentEvent";

export class SceneProcessor {
    constructor() {
        this.N = new MutIteratableCollection(ComponentEvent.comparator);
        this.q = new MutIteratableCollection(ComponentEvent.comparator);
        this.H = false;
        this.J = [];
        this.K = [];
    }
    addEventToNonSyncedCollection(t) {
        this.N.insert(t);
    }
    removeEventFromNonSyncedCollection(t) {
        this.N.delete(t);
    }
    addEventToSyncedCollection(t) {
        this.q.insert(t);
    }
    startProcessNonSyncedEvent() {
        this.N.forEach((t => t.invoke()));
    }
    tryStartProcessSyncedEvent() {
        if (this.H) return;
        this.H = true;
        this.q.forEach((t => t.invoke()));
        this.q.clear();
        this.H = false;
    }
    addRemoveGameObject(t) {
        this.J.push(t);
    }
    addRemoveComponent(t) {
        this.K.push(t);
    }
    processRemoveObject() {
        const t = this.K;
        for (let e = 0; e < t.length; e++) {
            t[e].gameObject.removeComponent(t[e]);
        }
        const e = this.J;
        for (let t = 0; t < e.length; t++) {
            e[t].removeFromParent();
        }
    }
}