import Set from "js-sdsl/dist/esm/Set/Set";

export class MutIteratableCollection {
    constructor(t) {
        this.ut = null;
        this.ft = new Set(undefined, t);
        this.dt = new Set(undefined, t);
        this.at = new Set(undefined, t);
        this.ct = new Set(undefined, t);
    }
    get size() {
        return this.ft.size();
    }
    insert(t) {
        t.isRemoved = false;
        if (this.ut !== null) this.dt.insert(t); else this.ft.insert(t);
    }
    delete(t) {
        if (this.ut !== null) {
            t.isRemoved = true;
            this.ct.insert(t);
        } else this.ft.eraseElementByValue(t);
    }
    clear() {
        this.ft.clear();
        this.dt.clear();
        this.ct.clear();
    }
    forEach(t) {
        this.ut = this.ft;
        this.ut.forEach((i => {
            if (!i.isRemoved) t(i);
        }));
        do {
            this.ut = this.St();
            this.ut.forEach((i => {
                if (!i.isRemoved) t(i);
            }));
        } while (0 < this.dt.size() || 0 < this.ct.size());
        this.ut = null;
    }
    St() {
        this.ut = null;
        this.ct.forEach((t => this.delete(t)));
        this.dt.forEach((t => this.insert(t)));
        const t = this.dt;
        this.dt = this.at;
        this.at = t;
        this.dt.clear();
        this.ct.clear();
        return t;
    }
}