export class TransformMatrixProcessor {
    constructor() {
        this.Xe = [];
        this.Ye = new Set;
        this.cr = new Set;
    }
    dr() {
        const t = this.Ye;
        this.Ye = this.cr;
        this.cr = t;
    }
    enqueueTransformToUpdate(t) {
        if (t.isRegisteredToProcessor) return;
        t.isRegisteredToProcessor = true;
        this.Xe.push(t);
    }
    enqueueRenderObject(t) {
        this.cr.add(t);
    }
    dequeueRenderObject(t) {
        this.cr.delete(t);
    }
    flush() {
        this.Ye.clear();
    }
    update() {
        const t = this.Xe;
        for (let e = 0; e < t.length; e++) {
            const s = t[e];
            s.tryUpdateWorldMatrixRecursivelyFromThisToChildren();
            s.isRegisteredToProcessor = false;
        }
        this.Xe.length = 0;
        this.dr();
        return this.Ye;
    }
}