import { RenderSetting } from "./RenderSetting";

import { PhysicsSetting } from "./PhysicsSetting";

export class GameSetting {
    constructor(t) {
        this.W = t;
        this.X = new RenderSetting(this.W.render);
        this.Y = new PhysicsSetting(this.W.physics);
    }
    static createDefaultObject() {
        return {
            render: RenderSetting.createDefaultObject(),
            physics: PhysicsSetting.createDefaultObject()
        };
    }
    get render() {
        return this.X;
    }
    get physics() {
        return this.Y;
    }
    make() {
        return this.W;
    }
}