export class PhysicsSetting {
    constructor(t) {
        this.Z = t;
    }
    static createDefaultObject() {
        return {};
    }
    loader(t) {
        this.Z.loader = t;
        return this;
    }
    gravity(t) {
        this.Z.gravity = t;
        return this;
    }
    defaultMaterial(t) {
        this.Z.defaultMaterial = t;
        return this;
    }
    velocityIterations(t) {
        this.Z.velocityIterations = t;
        return this;
    }
    positionIterations(t) {
        this.Z.positionIterations = t;
        return this;
    }
    queriesHitTriggers(t) {
        this.Z.queriesHitTriggers = t;
        return this;
    }
    reuseCollisionCallbacks(t) {
        this.Z.reuseCollisionCallbacks = t;
        return this;
    }
    layerCollisionMatrix(t) {
        this.Z.collisionLayerMaskMatrix = t;
        return this;
    }
}