class LinkedListNode {
    constructor(t) {
        this.next = null;
        this.prev = null;
        this.value = t;
    }
}

class LinkedList {
    constructor() {
        this.st = false;
        this.it = null;
        this.lt = null;
        this.ht = 0;
    }
    get length() {
        return this.ht;
    }
    add(t) {
        const s = new LinkedListNode(t);
        if (this.it === null) {
            this.it = s;
            this.lt = s;
        } else {
            this.lt.next = s;
            s.prev = this.lt;
            this.lt = s;
        }
        this.ht++;
    }
    nt(t) {
        if (t.prev !== null) {
            t.prev.next = t.next;
        } else {
            this.it = t.next;
        }
        if (t.next !== null) {
            t.next.prev = t.prev;
        } else {
            this.lt = t.prev;
        }
        this.ht--;
    }
    remove(t) {
        let s = this.it;
        while (s !== null) {
            if (s.value === t) {
                this.nt(s);
                return;
            }
            s = s.next;
        }
    }
    removeAll() {
        this.it = null;
        this.lt = null;
        this.ht = 0;
        this.st = true;
    }
    forEach(t) {
        this.st = false;
        let s = this.it;
        while (s !== null) {
            t(s.value);
            if (this.st) return;
            s = s.next;
        }
    }
}

export class EventContainer {
    constructor() {
        this.rt = new LinkedList;
    }
    addListener(t) {
        this.rt.add(t);
    }
    removeListener(t) {
        this.rt.remove(t);
    }
    removeAllListeners() {
        this.rt.removeAll();
    }
    invoke(...t) {
        this.rt.forEach((s => {
            s(...t);
        }));
    }
}